<template>
    <b-container class="content px-0 pt-4" v-if="episodes">
        <video-player class="py-3" :id="currentEpisode.id" :name="currentEpisode.name"></video-player>

        <span>
            <span class="text-white">Choisir épisode : </span>
            <b-select class="episode-chooser" v-model="$store.state.episode">
                <option v-for="episode in episodes" :key="episode.name">
                    {{ episode.name }}
                </option>
            </b-select>
        </span>
    </b-container>
</template>

<script>
    import * as axios from 'axios'

    export default {
        name: "Episodes",
        data() {
            return {
                episodes: null
            }
        },
        computed: {
            currentEpisode() {
                return this.episodes.filter(e => e.name === this.$store.getters.currentEpisode.toString()).pop();
            }
        },
        activated() {
            axios.get(process.env.VUE_APP_API_URL + '/drive/list').then(res => {
                this.episodes = res.data.sort((a, b) => parseInt(a.name) - parseInt(b.name));
            }).catch(err => {
                console.log(err.response.data);
            });
        }
    }
</script>

<style lang="scss" scoped>
    .content {
        height: 100%;
        background: rgba(255, 255, 255, 0.2);
    }

    .episode-chooser {
        width: 100px;
    }
</style>
