import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        episode: 1
    },
    getters: {
        currentEpisode(state) {
            if (state.episode)
                return state.episode;
            else
                return null;
        }
    },
    mutations: {
        setEpisode(state, episode) {
            state.episode = episode;
        }
    }
});
